//@ts-nocheck
import './scss/index.scss';

const parent = document.querySelector('.js-parallax');
const parallaxElems = document.querySelectorAll(`[data-parallax]`);
const parallaxBlock = document.querySelector(`.colours-hero__inner`);
const mobileBlock = document.querySelector(`.colours-hero__image`);

let jsEnabled = document.body.classList.contains('js-enabled');
const translateBlocksLeft = document.querySelectorAll(`[data-animleft]`);
const translateBlocksRight = document.querySelectorAll(`[data-animright]`);
const upBlocks = document.querySelectorAll(`[data-animup]`);
const fadeBlocks = document.querySelectorAll(`[data-animfade]`);

// console.log(translateBlocksLeft, translateBlocksRight, upBlocks, fadeBlocks);

const openMobileMenu = () => {
    const trigger = document.querySelector('.burger');
    const menu = document.querySelector('.header__nav');
    const items = menu.querySelectorAll('.header__link');

    trigger.addEventListener('click', (e) => {
        e.preventDefault();

        if(e.target.closest('.burger')) {
            trigger.classList.toggle('js-active');
        }

        trigger.classList.contains('js-active') ? menu.classList.add('js-active') : menu.classList.remove('js-active');
    })

    items.forEach(el => {
        el.addEventListener('click', (e) => {
            if(e.target.closest('.header__link')) {
                menu.classList.remove('js-active');
                trigger.classList.remove('js-active');
            }
        });
    })
}


const isLoadPage = () => {
    document.body.classList.add('js-enabled');
}

if(window.innerWidth > 576) {
    mobileBlock.style.display = 'none';
    parallaxBlock.style.display='flex';
    parent.addEventListener('mousemove', function(e) {
        let x = e.clientX / window.innerWidth;
        let y = e.clientY / window.innerHeight;
            parallaxElems.forEach(el => { 
                switch (el.dataset.parallax) {
                    case 'slow':
                        el.style.transform = 'translate(-' + x * 30 + 'px, -' + y * 70 + 'px)'
                        break;
                    case 'medium':
                        el.style.transform = 'translate(-' + x * 50 + 'px, -' + y * 90 + 'px)'
                        break;
                    case 'fast':
                        el.style.transform = 'translate(-' + x * 80 + 'px, -' + y * 120 + 'px)'
                        break;
                    case 'invert':
                        el.style.backgroundPosition =  x/-2 * 50 + 'px, ' + y * 100 + 'px';
                        break;
                    default:
                        el.style.transform = 'translate(-' + x * 30 + 'px,' + y * 75 + 'px)'
                        break;
                }
            })
    });
} else {
    parallaxBlock.style.display='none';
    mobileBlock.style.display = 'flex';
}

const translateAnim = () => {
        let observer = new IntersectionObserver((entries) => {
            entries.forEach((entry, i) => {
                if(entry.isIntersecting) {
                    console.log(entry.target);
                    entry.target.classList.add('js-anim-active');
                    entry.target.dataset.hasOwnProperty('animup') ? entry.target.style.animationDelay = (0.1 * (i+1)) + 's' : '';
                    observer.unobserve(entry.target)
                }
            });
        })
    
        translateBlocksLeft.forEach(block => observer.observe(block));
        translateBlocksRight.forEach(block => observer.observe(block));
        fadeBlocks.forEach(block => observer.observe(block));
        upBlocks.forEach(block => observer.observe(block));
}

window.addEventListener('DOMContentLoaded', (e) => {
    isLoadPage();
    translateAnim();
    openMobileMenu();
})

